import 'material-design-icons-iconfont/dist/material-design-icons.css'

<template>
  
  <div class="pa-3" id="tester">

      <v-dialog   v-model ="upLoadingDialog"      width="300">
    
           <v-card  elevation="16" class="mx-auto" min-width="300">
                  <v-card-title class="primary white--text">Uploading Meter Read</v-card-title>
                  <v-container style="height: 200px;">
              
                    <v-card-text>Percentage: {{  uploadedPercent }}</v-card-text>
                    <v-card-text>Total:      {{  uploadedTotal   }}</v-card-text>
                    <v-card-text>Loaded:     {{  uploadedLoaded  }}</v-card-text>

                  </v-container>
            </v-card>

      </v-dialog>
 
      <v-dialog  v-model = "previousMeterDialog"  width="auto">
    
                <v-card>
              
                  <v-card-title>
                    <span class="text-h5">Previous Meter - {{ this.customer['ACCOUNT'] }}</span>
                  </v-card-title>

                  <v-card-text>
                      <v-row>
                        <v-col cols="12">
                          <v-text-field disabled dense  v-model="previousReading.meter_id"       outlined label=  "Meter Identifier"  hint = "Meter Identifier"></v-text-field>
                          <v-text-field disabled dense  v-model="previousReading.meter_value"    outlined label=  "Meter Value"       hint = "Meter Value" ></v-text-field>
                          <v-text-field disabled dense  v-model="previousReading.meter_metric"   outlined label=  "Meter Metric"      hint = "Meter Metric"></v-text-field>
                          <v-text-field disabled dense  :value= "computedPreviousReadingDate"    outlined label = "Date Read"         hint = "Date Read"></v-text-field>
                          <v-textarea   disabled dense  v-model="previousReading.comments"       outlined label = "Comments:" readonly></v-textarea>
                          <v-btn        block class="mt-1 text-none"  @click ="showPreviousMeterLightBox">Show Photo</v-btn>
                        </v-col>
                      </v-row>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                     <v-btn   size="large" color="primary" class="text-none"   @click ="previousMeterDialog = false">Close</v-btn>
                  </v-card-actions>

              </v-card>

      </v-dialog>

      <vue-easy-lightbox 
            :visible   =  "lightBoxVisible" 
            :imgs      =  "lightBoxImage"
            @hide      =  "hideLightBoxImage"
      ></vue-easy-lightbox>
      
      <label class="d-none">
          <input id="file-input"         v-on:change="imageChange"   type="file" accept="image/*" capture >
      </label>
      
      <label class="d-none">
           <input id="file-input_gallery" v-on:change="imageChangeGallery" type="file" accept="image/x-png,image/jpeg,image/gif">
      </label>
      
      <v-row class="pa-2 mb-2 d-flex justify-space-between align-baseline">
                
            <div>Engineer Meter Reading</div>
            <div>   

              <v-btn  :loading = "loading" @click="showPreviousMeterDialog" class="mx-2" fab medium color="error">
                <v-icon dark>mdi-page-previous</v-icon>
              </v-btn>


            </div>

      </v-row>

      <v-row >
            <v-expansion-panels class="expansion_panels">
          
              <!-- customerComponent -->
                <customer v-bind:customer = "customer" />
              <!-- customerComponent -->

              <!-- Current Meter Reading -->
                 <v-expansion-panel class="expansion_pane mb-1"> 
                  <v-expansion-panel-header>
                    <div>Meter Read</div>
                  </v-expansion-panel-header>

                  <v-expansion-panel-content>

                    <v-row>
                        
                        <v-col cols="12" sm="6">

                          <v-form ref="meterReadForm" v-model="isFormValid">
                             <v-text-field :rules="meterIdRules"     required type="text"   clearable dense hint="Meter Identifier"   outlined v-model="meterReading.meter_id"         label="Meter Identifier"></v-text-field>
                             <v-text-field :rules="meterValueRules"  required type="number" clearable dense hint="Meter Reading"      outlined v-model="meterReading.meter_value"      label="Meter Value"></v-text-field>
                             <v-autocomplete
                                         :rules="meterMetricRules"
                                         required
                                         outlined
                                         clearable
                                         dense
                                         hint="Meter Metric"
                                         v-model="meterReading.meter_metric"
                                         :items="['Meters', 'Feet']"
                                         label="Meter Metric"
                             ></v-autocomplete>
                             <v-textarea  dense outlined v-model="meterReading.comments" label="Comments:"></v-textarea>
                             
                             <v-card-actions>
                                <v-btn class="text-none" :disabled="!isFormValid" :loading = "loading" @click="storeMeterRead" elevation="3" color="success" >Upload Meter Read</v-btn>
                                <v-spacer></v-spacer>
                                <v-btn class="text-none" @click="resetMeterReadForm" elevation="3" color="error" >Reset Form</v-btn>
                             </v-card-actions>
                         
                            </v-form>

                        </v-col>

                        <v-col  class="myborder pa-1" cols="12" sm="6">
                                           
                                           <v-card-actions>
                                                      <v-btn class="text-none" text color="primary" @click="takeImageCamera()">Camera
                                                      </v-btn>
        
                                                      <v-btn class="text-none" text color="primary" @click="takeImageGallery()">Gallery
                                                      </v-btn>
                                                                        
                                                      <v-spacer></v-spacer>
        
                                                      <v-btn class="text-none" text color="warning" @click="resetImage()">Reset
                                                      </v-btn>
        
                                           </v-card-actions>      
                
                                           <v-divider class="border-opacity-25"></v-divider>
                
                                           <img    class   = "myimg" 
                                                   @click  = "showLightBoxImage()"
                                                   :src    = "meterImage"
                                           />
         
                        </v-col>
                      
                    </v-row>


                  </v-expansion-panel-content>
                </v-expansion-panel>
              <!-- Current Meter Reading -->


              <!-- last one for space-->
                <v-expansion-panel class="expansion_panel mb-2"> 
                </v-expansion-panel>
              <!-- last one for space-->

            </v-expansion-panels>

     </v-row>

  </div>

</template>

<script>


import {store}   from '../../main.js'
import Localbase from 'localbase'

export default {

   name     : "meterRead",
   formName : 'meterRead',

    destroyed () {
       
          store.consoleLog( "meterRead form destroyed");

           this.customer            = JSON.parse( localStorage.getItem( "equalResponseCustomer") );
           this.engineer            = JSON.parse( localStorage.getItem( "equalResponseEngineer") );
           this.meterReading.engineer_id      = this.engineer['ENGINEER_ID'];
           this.meterReading.customer_id      = this.customer['ACCOUNT'];

           this.createSnapShot();
    },
      
    created    (){ 
 
         store.consoleLog( "meterRead form created");

         this.customer                    = JSON.parse( localStorage.getItem( "equalResponseCustomer") );
         this.engineer                    = JSON.parse( localStorage.getItem( "equalResponseEngineer") );
         this.meterReading.engineer_id    = this.engineer['ENGINEER_ID'];
         this.meterReading.customer_id    = this.customer['ACCOUNT'];
         this.restoreSnapShot();
    }, 


   methods   : {

              resetMeterReadForm () {
                this.$refs.meterReadForm.reset();
                this.resetImage();
              },

              showPreviousMeterLightBox() {
                    this.lightBoxImage          = this.previousReading.meter_image;
                    this.lightBoxVisible        = true
              },
          
              showPreviousMeterDialog() {
              
                const meterURL  =  this.$restServer.meter;
                let   URL       =  meterURL + "/" + this.customer['ACCOUNT'];
                URL             =  URL.toLowerCase();
 
                this.loading = true;

                this.$http.get( URL )
                .then(  (response) => {
                    this.previousReading         = response.data;
                    this.loading                 = false;
                    this.previousMeterDialog     = true;

                  })
                .catch( (e) => {
                  this.loading                  = false;
                  this.showSnackbar(e)
                 });
                 
              },

              storeMeterRead() {

                  const meterURL        = this.$restServer.meter;
                  this.loading          = true;
                  this.upLoadingDialog  = true;


                  let   formData     =  new FormData();
                  formData.append( 'meterImage',     this.meterImage);
                  formData.append( 'meterReading',   JSON.stringify(this.meterReading) );

                  this.$http.post(meterURL, formData, {
                 
                        headers          : { 'Content-Type': 'multipart/form-data'},

                        onUploadProgress : ( progressEvent ) => {
                           this.uploadedPercent          = parseInt(Math.round( (progressEvent.loaded / progressEvent.total) * 100));
                           this.uploadedTotal            = progressEvent.total;
                           this.uploadedLoaded           = progressEvent.loaded;
                       }   
                 
                  }).then(  (response) => {

                     console.log(response); 

                     this.loading          = false;
                     this.upLoadingDialog  = false;
                     this.resetMeterReadForm();
                  

                  }).catch( (e) => { 
                     this.loading          = false;
                     this.upLoadingDialog  = false;
                     alert(e);
                  });

              },
                   
              showUploadingDialog() {
                    this.uploadingDialog = true;
              },
    
              closeUploadingDialog() {
                          setTimeout( () => {
                              this.uploadingdDialog  = false;
                          }, 2000)
              },
              
              createSnapShot(){

                let snapShot             =  this.generateSnapShot();
                let db                   =  new Localbase( this.$dbName );

                db.collection( this.collection ).doc( this.meterReading.customer_id).set( snapShot )
                .then(response => { 
                   console.log( response)
                })
                .catch(error => {
                   console.log( error)
                })
              },

              restoreSnapShot(){

                let db = new Localbase(  this.$dbName );
                db.collection( this.collection).doc( this.meterReading.customer_id).get()
                .then( document => {
                  if ( document != null ) {
                         this.meterReading        = document['meterReading'];
                         this.previousReading     = document['previousReading'];
                         this.meterImage          = document['meterImage'];
                         
                  }
                })
                .catch( error => {
                   console.log( error)
                });

              },

              generateSnapShot() {
                                let snapShot          =   { 
                                                             'meterReading'        : this.meterReading,
                                                             'previousReading'     : this.previousReading,
                                                             'meterImage'          : this.meterImage,

                                                          };
                                return snapShot;
              },

              takeImageGallery() {
                    document.getElementById('file-input_gallery').click();
              },

              takeImageCamera() {
                    document.getElementById('file-input').click();
              },

              resetImage() {

                    this.meterImage  = this.$restServer['blankMeterPhoto'];
                    this.createSnapShot();

              },

              async imageChangeGallery() {
                                          
                                          try {
                                         
                                              var file     = document.getElementById("file-input_gallery");
                                              if (file.files.length > 0 ) {
                                               
                                                  const photo              = document.getElementById("file-input_gallery").files[0];
                                                  const dataUrl            = await this.getDataUrl( photo  );
                                                  this.meterImage          = dataUrl;
                                                  this.createSnapShot();
                                                  document.getElementById('file-input_gallery').value = null;
                                              }
                      
                                          }catch (error) {
                                              console.log(error); 
                                          }
                         
              },
                      
              async imageChange() {
                                          
                    try {
                   
                        var file     = document.getElementById("file-input");
                        if (file.files.length > 0 ) {
                         
                            const photo            = file.files[0];
                            const dataUrl          = await this.getDataUrl( photo );
                            this.meterImage        = dataUrl;
                            this.createSnapShot();
                            document.getElementById('file-input').value = null;
                        }

                    }catch (error) {
                        console.log(error); 
                    }
   
              },
           
              getDataUrl( photo ) {

                        return new Promise(function(resolve, reject) {
                          
                          const reader = new FileReader();
                          reader.readAsDataURL(photo);
                          
                          reader.addEventListener('load',  function(e) {
                              const dataUrl = e.target.result; 
                              resolve( dataUrl );
                          });

                          reader.addEventListener('error', function(e) {
                             reject(e);
                          });

                        });
              },

              showLightBoxImage() {
                    this.lightBoxImage          = this.meterImage;
                    this.lightBoxVisible        = true
              },
     
              showPreviousLightBoxImage() {

                    this.lightBoxImage          = this.computedPreviousReadingMeterImage;
                    this.lightBoxVisible        = true
              },
                   
              hideLightBoxImage() {
                    this.lightBoxImage          =  null;
                    this.lightBoxVisible        =  false
              },
 
   },

    computed  :{
            computedPreviousReadingDate() {
              
              if ( this.previousReading.created_at ) {

                   const date = new Date(this.previousReading.created_at);
                   return date.toLocaleString('en-GB');

              } else {
                return "";

              }
         
            },
            computedPreviousReadingMeterImage() {
                return (this.previousReading.meter_image ) ? this.previousReading.meter_image : ""
            },
    },

   data() {

    return {
        
         MySnackBar        : { "show" : false, "text" : "Default Text", "showImage" : "icon"},

         loading                  : false,
         upLoadingDialog          : false,
         previousMeterDialog      : false,
         isFormValid              : false,

         uploadedPercent          :  0, 
         uploadedTotal            :  0, 
         uploadedLoaded           :  0,

         meterIdRules: [
            v => !!v || 'Identifier is required',
            v => (v && v.length <= 10) || 'Identifier must be less than 10 characters',
         ],

         meterValueRules: [
            v => !!v || 'Meter Value is required',
            v => (v && v.length <= 10) || 'Value must be less than 10 characters',
         ],

         meterMetricRules: [
            v => !!v || 'Meter Metric is required',
            v => (v && v.length <= 10) || 'Value must be less than 10 characters',
         ],


         lightBoxVisible           : false,
         lightBoxImage             : null,
         formName                  : "meterRead",
        
         customer          : {},
         salesman          : {},
         engineer          : {} ,

        collection        :  "meterRead",

        previousReading          : {
                                      engineer_id       :  "",
                                      customer_id       :  "",
                                      meter_id          :  "",
                                      meter_value       :  "",
                                      meter_metric      :  "",
                                      meter_image       :  "",
                                      comments          :  "",
                                      created_at        :  "",
                                      updated_at        :  "",
        },

        meterImage       :   this.$restServer['blankMeterPhoto'],
        meterReading     :  {
                              engineer_id      :  "",
                              customer_id      :  "",
                              meter_id         :  "",
                              meter_value      :  "",
                              meter_metric     :  "",
                              comments         :  "",
                            },

        };
  }

};


</script>

<style scoped>

.myimg {

  object-fit : fill !important;
  width      : 100%  !important;
  height     : auto  !important;
  margin     : auto;
}

.myborder {
  border        : 1px solid black;
  border-radius : 4px !important;
}

.row {
  display: flex; /* equal height of the children */
}

.col {
  flex: 1; /* additionally, equal width */
}

.dcontents{
  display: contents !important;
}

h1,h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.dl_expansion_panel_content>>> .v-expansion-panel-content__wrap {
  padding: 0 !important;
}

.header{
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: bold;
}

input[type=text] {
  width: 100%;
  padding: 8px 20px;
  margin: 2px 0;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}

input[type=text]:focus {
  border: 3px solid #555;
}

div.v-text-field__details {
  display: none;
}

legend.v-label {
  font-size: 16px !important;
}

.fill {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden
}
.fill img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%
}


</style>
       
